import React, {Component} from 'react';
import './Operation.css';

class Operation extends Component{
	
	state = {
		operationStr: []
	}

	expressionBuildHandler(e){ 
		let data = this.state.operationStr.slice()
		let operand = e.target.innerHTML
		if(operand === "ยฑ") { data.push("x", "-1") }
		else { data.push(operand) }
		this.setState( {operationStr:data} )
		this.props.showExp(data.join(''))
	}

	solveHandler(){ 
		let data = this.state.operationStr.slice()
		this.props.solve(data) 
		this.setState({operationStr:[]})
	}

	clearHandler = () => {
		this.setState({operationStr:[]})
		this.props.clear()
	}
	
	render(){
		return (
			<div>	
				<div className="row">
					<div onClick={this.clearHandler.bind(this)} className="options">C</div>
					<div className="options" onClick={this.expressionBuildHandler.bind(this)} >ยฑ</div>
					<div className="options" onClick={this.expressionBuildHandler.bind(this)} >%</div>
					<div className="operators" onClick={this.expressionBuildHandler.bind(this)} >รท</div>
				</div>
			
    			<div className="row">
					<div className="digits" onClick={this.expressionBuildHandler.bind(this)} >7</div>
					<div className="digits" onClick={this.expressionBuildHandler.bind(this)} >8</div>
					<div className="digits" onClick={this.expressionBuildHandler.bind(this)} >9</div>
					<div className="operators" onClick={this.expressionBuildHandler.bind(this)} >x</div>
				</div>
			
				<div className="row">
					<div className="digits" onClick={this.expressionBuildHandler.bind(this)} >4</div>
					<div className="digits" onClick={this.expressionBuildHandler.bind(this)} >5</div>
					<div className="digits" onClick={this.expressionBuildHandler.bind(this)} >6</div>
					<div className="operators" onClick={this.expressionBuildHandler.bind(this)} >-</div>
				</div>

				<div className="row">
					<div className="digits" onClick={this.expressionBuildHandler.bind(this)} >1</div>
					<div className="digits" onClick={this.expressionBuildHandler.bind(this)} >2</div>
					<div className="digits" onClick={this.expressionBuildHandler.bind(this)} >3</div>
					<div onClick={this.expressionBuildHandler.bind(this)} className="operators">+</div>
				</div>

				<div className="row">
					<div id="zero" className="digits" onClick={this.expressionBuildHandler.bind(this)}>0</div>
					<div id="decimal"className="digits" onClick={this.expressionBuildHandler.bind(this)}>.</div>
					<div id="equal" className="operators" onClick={this.solveHandler.bind(this)}>=</div>
				</div>

			</div>
			
		);
	}
}

export default Operation;


