import React, { Component } from 'react';
import Operation from './components/Operation/Operation';
import Result from './components/Result/Result';
import './App.css';

class App extends Component {

	state = {
		result:0,
		exp:''
	}
	
	clearResultHandler = () => {
		this.setState({
			result:0,
			exp:null
		})
	}

	showExp = (e) => {
		let cur = this.state.result.toString()
		if(cur !== '0'){
			let data = cur + e
			this.setState({ exp:data })
		}
		else{
			this.setState({ exp:e })
		}
	}

	calculateResultHandler = (exp) => {
		try {
			const { evaluateInfix } = require('calculator-lib')
			if(this.state.result !== 0){ exp.splice(0, 0, this.state.result.toString()) }
			let reducer = (accumulator, currentValue) => accumulator + currentValue
			let infix = exp.reduce(reducer) //expression
			let newResult = evaluateInfix(infix) //calulate it
			this.setState({	result:newResult })
		} catch(err){
			this.setState({	
				exp:"Invalid expression"
			}) 
		}
	}

  render() {
    return (
      <div className="App">
			<div className="App-screen">
				<span className="App-exp">{this.state.exp}</span>
				<Result output ={this.state.result}/> 
			</div>
			
			<Operation 
				showExp={this.showExp}
				solve={this.calculateResultHandler}
				clear={this.clearResultHandler}/>
      </div>
    );
  }
}

export default App;



